import {Box,Stack,Button} from '@mui/material';
import { Typography } from '@mui/material'
import '../Chats/style.css'
import React, { useContext, useEffect, useState,useRef  } from 'react';
import MyChats from '../Chats/MyChats'
import ChatBox from '../Chats/ChatBox'
import SideDrawer from '../miscellaneous/SideDrawer';
import { useNavigate } from 'react-router-dom';
import {ChatState }from '../../Context/ChatProvider'
import Inbox from '../Chats/Inbox';
import { UserContext } from '../../App'
import {io}  from "socket.io-client"

function Chat() {  
  const scrollRef =  useRef()
  const socket = useRef(io("http://localhost:3000"))
  const {state} = useContext(UserContext)
  const [chats, setChats] = useState([])
  const [fetchAgain, setFetchAgain] = useState(false)
  const [onlineUsers, setonlineUsers] = useState([])
  const navigate = useNavigate();

  const [currentChat, setCurrentChat] = useState(null)
  const [messages, setMessages] = useState([])
  const [newMessage, setNewMessage] = useState("")

  //arrived messages : senders
  const [arrMesg, setArrMesg] = useState(null)

  useEffect(() => {
    socket.current.on("getMessage",(data)=>{
      console.log("sender arr []",data);
      setArrMesg({
        sender:data.senderId,
        text:data.text,
        createdAt:Date.now()
      })
    })
  }, [])

  useEffect(() => {
    console.log("E O U",onlineUsers);
  }, [onlineUsers])
  

  useEffect(() => {
    
   console.log("state",arrMesg)
   console.log("C Chat",currentChat)
    arrMesg && currentChat?.users.includes(arrMesg.sender) &&
    setMessages((prev) => [...prev, arrMesg])
   var j = arrMesg && currentChat?.users.includes(arrMesg.sender)
    console.log("io att and include",j ) 
  }, [arrMesg,currentChat])
  
//prevent null null saved

  useEffect(() => {
    console.log("chat so user",state._id);
    socket.current.emit("addUser", state._id)
    socket.current.on("getUsers",users => {
     setonlineUsers(state.following.filter((f)=> users.some((u)=> u.userId === f) ) )
    })
  }, [state])

  //console.log("mess",messages);
 
  useEffect(()=>{
    fetch('/fetchchat/'+state._id, {
      headers: {
        "Authorization": "Bearer " + localStorage.getItem("jwt")
      }
    }).then(res => res.json())
    .then(result => {
     
      setChats(result)
     
    })

  },[state._id])
  
  useEffect(() => {
   fetch('/messages/'+currentChat?._id,{
    headers:{
      "Authorization":"Bearer" + localStorage.getItem("jwt")
    }
   }).then(res => res.json())
   .then(result => { 
    setMessages(result)    
   // console.log("set mess",result)
   })
  }, [currentChat])

  const reciverId = currentChat?.users.find(user => user !== state._id)
  
  const handelSubmit = (e) =>{
    e.preventDefault()
    socket.current.emit("sendMessage",{
      senderId: state._id,
      reciverId,
      text:newMessage,
    })

    fetch('/message',{
      method:'post',
      headers:{
        "Content-type":"application/json"
      },
      body:JSON.stringify({
        sender:state._id,
        text:newMessage,
        conversationId:currentChat._id
      })
    }).then(ress=>  ress.json())
    .then(data=>{  
      // console.log(data)
      setMessages([...messages,data])
      setNewMessage("")
    })
  }

  useEffect(() => {
    scrollRef.current?.scrollIntoView({ behavior:"smooth"})
  }, [messages])
 
  return (
    <>    
    <SideDrawer/>
    <Box
      sx={{
        display: "flex",
        justifyContent: "space-between",
        width: "100%",
        height: "75vh",
        padding: "10px",
      }}
      >  <Box
      sx={{
        display: { xs: true ? "none" : "flex", md: "flex" },
        flexDirection: "column",
        alignItems: "center",
        padding: "3px",
        background: "white",
        width: { xs: "100%", md: "31%" },
        borderRadius: "10px",
        borderWidth: "1px",
      }}
    >   <Box
      className="heading-chat"
      sx={{
        paddingBottom: "3px",
        paddingX: "3px",
        display: "flex",
        width: "100%",
        justifyContent: "space-between",
        alignItems: "center",
      }}
    ><Typography fontSize={30}>My chats</Typography>
        <div>
          <Button variant="contained"
          //endIcon={<AddIcon />}
          >Create Group
          </Button>
        </div>
      </Box>
      <Box
        sx={{
          display: "flex",
          flexDirection: "column",
          padding: "3px",
          backgroundColor: "#F8F8F8",
          width: "100%",
          height: "100%",
          borderRadius: "10px",
          msOverflowY: "hidden",
        }}
      >
        <Stack sx={{
          overflowY: "scroll",
        }}
        >
          { chats?.map((c)=>(            
            <div onClick={() =>{ setCurrentChat(c) } }>                    
              <MyChats  fetchChat={c}  currentUser={state} isTrue={c._id  === currentChat?._id}/>            
            </div>
           )) 
          }
        </Stack>
      </Box>
      </Box>



        <div  style={{width:"50%",paddingRight:10}} className="chatBoxWrap">
        {
          currentChat ?
          <> 
            <div  style={{height: "75%",overflowY:"scroll",paddingRight:10}}>
            {
            messages.map(m=>(
              <div ref={scrollRef} className="scrollEffect">
                <Inbox message={m} own={m.sender === state._id} />
              </div>
            ))
            }
            </div>  

            <div className="chatBoxBottom">
              <textarea className='chatMessageInput' placeholder='write something..' onChange={(e)=> setNewMessage(e.target.value)}
              value={newMessage}></textarea>
              <button className='chatSubmitButton'
              onClick={ handelSubmit }>send</button>
            </div> 
          </>
          :
          <span className="noConv">
            Open a conversation to chat.
          </span> 
        }  
        </div>  
          
        <ChatBox onlineUsers={onlineUsers} currentId={state._id} setCurrentChat={setCurrentChat}  fetchAgain = { fetchAgain }  setFetchAgain = { setFetchAgain } />
      </Box>
</>
  )
}

export default Chat