import React,{useEffect, useContext , createContext, useReducer, useState } from 'react'
import { BrowserRouter as Router, Route , Routes ,useNavigate,useLocation } from 'react-router-dom'
// ? //location.pathname
import "./App.css"
import Navbar from "./components/Navbar";
import Meterial from "./components/meterial/Meterial";
import Home from "./components/screen/Home";
import Signup from "./components/screen/Signup";
import Signin from "./components/screen/Signin";
import Profile from "./components/screen/Profile";
import UserProfile from './components/screen/UserProfile';
import CreatePost from './components/screen/CreatePost';
import { reducer , initialState } from './reducer/userReducer';
import SubscribesUserPost from './components/screen/SubscribesUserPost';
import Newpassword from './components/screen/Newpassword';
import Reset from './components/screen/Reset';
import Chat from './components/screen/Chat';
//import io from "socket.io-client"
export const UserContext = createContext()

//swhich for chech active route kar karam karee ... 
const Routing = () =>{
  const history = useNavigate() 
  const location = useLocation();
  
  const [eio, setEio] = useState("")
  const {state, dispatch} = useContext(UserContext)
  console.log("eo",state  )

//   useEffect(() => {
//     const socket = io("http://localhost:5000")      

//  }, [])
  
  useEffect(()=>{
    const user = JSON.parse(localStorage.getItem("user"))    
    if(user){
      dispatch({type:"USER",  payload:user})      
    }else{
      if( ! location.pathname.startsWith('/reset')){
      history('/signin')
      }  
    }
  },[])

  return(
    <Routes>      
      <Route path='/' element={<Home />}/>     
      <Route path='/pro' element={<Meterial />}/>     
      <Route path='/signup' element={<Signup />} />
      <Route path='/signin' element={<Signin />} />
      <Route exact path='/profile' element={<Profile />} />
      <Route path='/profile/:userId' element={<UserProfile />} />
      <Route path='/myFollowingPost' element={<SubscribesUserPost />} />
      <Route path='/create' element={<CreatePost />} />
      <Route path='/reset' element={<Reset />} />
      <Route path='/chat'
        element={state?  <Chat/>: <Home /> }
        />
      <Route exact path='/reset/:token' element={<Newpassword />} />
    </Routes>
  )
}

function App() {
  const [state,dispatch] = useReducer(reducer,initialState)
  return( 
  <UserContext.Provider value={{state,dispatch}} >
  <Router>
    <Navbar />
    <Routing />
 
  </Router>
  </UserContext.Provider>
  )
}

export default App