import React, { useEffect, useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import M from 'materialize-css'

const Signup = () => {
   const navigate = useNavigate()
    const [password, setPassword] = useState("")
    const [email, setEmail] = useState("")
    const [name, setName] = useState("")
    const [image, setImage] = useState("")
    const [url, setUrl] = useState(undefined)
    
    useEffect(() => {
      if(url){
        uploadFields()
      }
    }, [url])
    
    const uploadFields = ()=>{
 
        if(!/^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(email)){
            return M.toast({html: "Invalid Email",classes:"#e53935 red darken-1"})
            }
        fetch('/signup',{
            method:"post",
            headers:{
                "Content-type":"application/json"
            },
            body:JSON.stringify({
                name,
                email,
                password,
                pic: url 
            })
        }).then(ress=>  ress.json())
        .then(data=>{          
            if(data.err){  
                    M.toast({html: data.err,classes:"#e53935 red darken-1"})
            }else{
                M.toast({html:data.message,classes:"#43a047 green darken-1"})
                navigate('/signin')
            }
        })
        // .catch(err=>{
        //     console.log(err);
        // })
    }
    const uploadPic = ()=>{

        const data = new FormData()
        data.append("file", image)
        data.append("upload_preset", "my-app")
        data.append("cloud_name", "sanil123")
        fetch("https://api.cloudinary.com/v1_1/sanil123/image/upload", {
            method: "post",
            body: data
        })
            .then(res => res.json())
            .then(data => {
                setUrl(data.url)
                console.log("data", data);
                // console.log("title",title);
                // console.log("body",body);
            })
            .catch(err => console.log(err))
    }
    const PostData = ()=>{
        if(image){
            uploadPic()
        }else{
            uploadFields()
        }
    }
    return(
        <div className="mycard">
        <div className='auth-card input-field'>
            <div className="card">
                <h2>Collagebook</h2>
                <input type="text" placeholder='name'  value={name}
                    onChange={(e)=>setName(e.target.value)}/>
                <input type="text" placeholder='email'  value={email}
                    onChange={(e)=>setEmail(e.target.value)}/>
                <input type="password" placeholder='password'  value={password}
                    onChange={(e)=>setPassword(e.target.value)}/>
                <div className="file-field input-field">
                <div className="btn">
                    <span>upload profle pic</span>
                    <input
                        type="file"
                        onChange={(e) => {
                            console.log(e.target.files)
                            setImage(e.target.files[0])
                        }} />
                </div>
                <div className="file-path-wrapper">
                    <input className="file-path validate" type="text" />
                </div>
                </div>
                <button onClick={PostData} className="btn waves-effect waves-light #4db6ac teal darken-1">Sign Up
                </button>
                <h5  className='p-2'>
                    <Link to="/signin" >Already have an Account ?</Link>
                </h5>
            </div>
        </div>
    </div>
    )
}

export default Signup