import React, { useEffect, useState, useContext } from 'react'
//import { put } from '../../../../Server/routes/post'

import { UserContext } from '../../App'

const Home = () => {
  const [data, setData] = useState([])
  const { state, dispatch } = useContext(UserContext)
  const [liked, setLiked] = useState(false)
  console.log("da",data);
  useEffect(() => {
    fetch('/allpost', {
      headers: {
        "Authorization": "Bearer " + localStorage.getItem("jwt")
      }
    }).then(res => res.json())
      .then(result => {
       
        setData(result.posts)
      })
  }, [])


  const likePost = (id) => {
    fetch('/like', {
      method: 'put',
      headers: {
        "Content-Type": "application/json",
        "Authorization": "Bearer " + localStorage.getItem('jwt')
      },
      body: JSON.stringify({
        postId: id
      })
    }).then(res => res.json())
      .then(result => {
        const newData = data.map(item => {
          if (item._id === result._id) {
            return result
          } else {
            return item
          }
        })
       setData(newData) 
      })
      .catch(err => {
        console.log(err);
      })
  }
  const unlikePost = (id) => {
    fetch('/unlike', {
      method: 'put',
      headers: {
        "Content-Type": "application/json",
        "Authorization": "Bearer " + localStorage.getItem('jwt')
      },
      body: JSON.stringify({
        postId: id
      })
    }).then(res => res.json())
      .then(result => {       
        const newData = data.map(item => {
          if (item._id === result._id) {
            return result
          } else {
            return item
          }
        })
        setData(newData)
      }).catch(err => {
        console.log(err);
      })
  }

  const makeComment = (text, postId) => {
    fetch('/comment', {
      method: "put",
      headers: {
        "Content-Type": "application/json",
        "Authorization": "Bearer " + localStorage.getItem("jwt")
      },
      body: JSON.stringify({
        postId,
        text
      })
    }).then(res => res.json())
      .then(result => {        
        const newData = data.map(item => {
          if (item._id === result._id) {
            return result
          } else {
            return item
          }
        })
        setData(newData)
      }).catch(err => console.log(err))
  }
  const deletePost = (postId) => {
    fetch(`/deletePost/${postId}`, {
      method: 'delete',
      headers: {
        Authorization: "Bearer " + localStorage.getItem('jwt')
      }
    }).then(res => res.json() )
      .then(result => {
      console.log(result.result)
        const newData = data.filter(item => {
          return item._id !== result.result._id
        })

          setData(newData) 

      })
      .catch(err => {
        console.log(err);
      })
  }
const deleteComment = (postId)=>{
console.log("del");
  fetch(`/deletetext/${postId}`,{
    method:"delete",
    headers:{
      Authorization:"Bearer "+ localStorage.getItem('jwt')
    }
  }).then(res=> res.json())
  .then(result => {
    console.log(result.saved)
          
      const newData = data.map(item => {
        if (item._id === result.saved._id) {
          return result.saved
        } else {
          return item
        }
      })
      console.log(newData);
      setData(newData)
  })
  .catch(err =>{
   // M.toast({html:err.error,})
    console.log("422",err);
  })
}
  return (
    <div className='home'>
      {
        data.map(item => {
          return (
            <div className="card home-card" key={item._id} >
              <h5 style={{padding:"5px"}} > <img className='messageImg' style={{width: "32px",height:"32px",borderRadius: "50%",objectFit: "cover",marginRight: "10px"}}
              src={item.pic}/>
                {item.postedby.name}
                {
                  item.postedby._id === state._id
                    &&
                  <i className="material-icons" style={{ float: 'right' ,cursor:"pointer"}}
                    onClick={() => deletePost(item._id)} >delete</i>
                }
              </h5>
              <div className="card-image">
                <img src={item.photo} alt="flower" />
              </div>
              <div className="card-content" style={{paddingBottom:0}}>
               
               
                {

                  item.likes.includes(state._id)

                    ? <i className="small material-icons like"
                      onClick={() => { unlikePost(item._id); console.log(state._id) }}style={{ color: "red" }}>favorite</i>

                    : <i className="small material-icons like"
                      onClick={() => { likePost(item._id); console.log(state) }}style={{ color: "black" }}>favorite_border</i>
                }
                <span><i class="small material-icons like" style={{paddingLeft:10,fontSize:27}}>
                  send</i>
                </span><span class="small material-icons" style={{paddingLeft:10,fontSize:27}}>
                chat_bubble_outline
                </span>
                <i onClick={dispatch({ status: 'false' })} ></i>
                <h6>{item.likes.length} Likes </h6>
                <div>

                <span><h6>{item.title}</h6> 
                <h6>#{item.body}</h6> </span>
                </div>
                {
                  item.comments.length > 0 ? 
                 
                    item.comments.map(record => {
                      return (                        
                        <div >
                        <h6 key={record._id}><span style={{ fontWeight: "500" }}> <i class="tiny material-icons">account_circle</i>{record.postedby.name} </span><span style={{backgroundColor:"#8080803b" , borderRadius:5}} > {record.text} </span>
                        <span>
               { state._id === record.postedby._id
                    &&
               <i className="material-icons" style={{ float: 'right' ,cursor:"pointer"}}
               onClick={() => deleteComment(record._id)} >delete</i>
                    } 
               </span>  </h6></div>
                      )
                    })
                    :
                    <p style={{color:"grey"}}>No comments yet!</p>
                }

                <form onSubmit={(e) => { 
                  e.preventDefault()
                  makeComment(e.target[0].value, item._id);
                }}>
              
                  <input type="text" placeholder="add a comment" style={{backgroundColor:" rgb(104 99 99 / 3%)" ,border:"none"}} />
                </form>
              </div>
            <hr style={{position: "absolute", bottom: 38, width: "100%"}}/>
            </div>
          )
        })
      }
    </div>
  )
}

export default Home