import './style.css'
import React, { useEffect, useState } from 'react'
import { Avatar, Box,  List, ListItem, ListItemAvatar, ListItemText, Stack } from '@mui/material'


function MyChats({ fetchChat, currentUser ,isTrue}) {
  console.log(isTrue)
  const [user, setUser] = useState(null)
  useEffect(() => {
   
    const friendId = fetchChat.users.find((m)=> m !== currentUser._id)
    
    fetch('/user/'+friendId,{
      headers:{
        "Authorization":"Bearer" + localStorage.getItem("jwt")
      }
    }).then(res=> res.json())   
    .then(result =>{
    setUser(result.user)
    })
  }, [currentUser,fetchChat])

  return (
    <>
    


            <Box
              sx={{
                cursor: "pointer",
                // background:
                //   selectedChat === chat ? "#38B2AC" : "E8E8E8",
                // color: selectedChat === chat ? "white" : "black",
                paddingX: "3px",
                paddingY: "2px",
                borderRadius: "10px",
                borderBottom: "1px solid #f1f1f1",
              }}>
              <List
                dense
                sx={{
                  width: "100%",
                  bgcolor: "background.paper",
                  background:
                    //selectedChat === chat 
                    isTrue? "#38B2AC" : "E8E8E8",
                }}
              >
                <ListItem>
                  <ListItemAvatar>
                    <Avatar
                      alt="G"
                      src={user ? user.pic : 'http://res.cloudinary.com/sanil123/image/upload/v1670152337/obtdaf3f9wwpscyfu2ne.jpg'}>

                    </Avatar>
                  </ListItemAvatar>
                  <ListItemText
                    primary={user ? user.name:""}
                  // primary={
                  //     !chat.isGroupChat
                  //       ? getSender(loggedUser, chat.users)
                  //       : chat . chatName
                  //   }
                  />
                </ListItem>
              </List>
            </Box>




        
    </>
  )
}

export default MyChats