  import React, { useContext, useEffect, useState } from 'react'
import { UserContext } from '../../App'
import "../../App.css"
const Profile = () => {
    const [pics, setPics] = useState([]);
    const { state, dispatch } = useContext(UserContext)
    const [image, setImage] = useState("")
    const [url, setUrl] = useState(undefined)
    const [followers, setFollowers] = useState(state ?state.followers:[])
    const [following, setFollowing] = useState(state ?state.following:[])
    console.log("new state",followers,following)
    console.log("state", state);
    useEffect(() => {
        fetch('/mypost', {
            headers: {
                "Authorization": "Bearer " + localStorage.getItem("jwt")
            }
        }).then(res => res.json())
            .then(result => {
                console.log(result);
                setPics(result.mypost)
            })
    }, [])
    useEffect(() => {    
    if(image){
        const data = new FormData()
        data.append("file", image)
        data.append("upload_preset", "my-app")
        data.append("cloud_name", "sanil123")
        fetch("https://api.cloudinary.com/v1_1/sanil123/image/upload", {
            method: "post",
            body: data
        })
            .then(res => res.json())
            .then(data => {
                setUrl(data.url)
                localStorage.setItem("user",JSON.stringify({...state, pic:data.url}))
                dispatch({type:"UPDATEPIC",payload:data.url})
                console.log("data", data);
                // console.log("title",title);
                // console.log("body",body);
            })
            .catch(err => console.log(err)) 
    }
    }, [image])
    
    const uploadPhoto= (file)=>{
        setImage(file)        
    }
    return (
        <div style={{ maxWidth: "550px", margin: "0px auto", borderBottom:"1px solid grey"}}>
            <div style={{margin:"18px 0px" ,}}> 
               
            <div style={{
                display: "flex", justifyContent: "space-arround", margin: "18px 0px", 
            }
            }>
                
                <div className="c">
                    <img style={{ width: "160px", height: "160px", borderRadius: "80px" }} 
                    src={state ? state.pic: "loading.."}
                    alt="pic" />                    
                    
                </div>
                <div className="c">
                    <h4>{state ? state.name : ". . . "}</h4>
                    <h4>{state ? state.email : ". . . "}</h4>
                    <div className="" style={{ display: 'flex', justifyContent: "space-between", width: "100%" }}>
                        <h5 className='follow'> {pics.length} post </h5>
                        <h5 className='follow'> {state ? followers.length : "0"} followers </h5>
                        <h5 className='follow'> {state ? following.length : "0"} following </h5>{/*{}{}? {state.followers.length}  {state.following}? {state.following.length} following: */}
                    </div>
                </div>
            </div>                
            </div>
                 
                <div className="file-field input-field" style={{margin:"10px"}}>
                <div className="btn">
                    <span>upload profle pic</span>
                    <input
                        type="file"
                        onChange={(e) => {
                            console.log(e.target.files)
                            uploadPhoto(e.target.files[0])
                        }} />
                </div>
                
                <div className="file-path-wrapper">
                    <input className="file-path validate" type="text" />
                </div>
                </div>


            <div className="gallery">
                {
                    pics.map(item => {
                        return (
                            <img className='item' key={item._id} src={item.photo} alt={item.title} />
                        )
                    })
                }

            </div>
        </div>
    )
}

export default Profile