import React, { useContext , useEffect, useRef, useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import { UserContext } from '../App'
import M from 'materialize-css'
import Notificatoin from '../img/setting.png'
import Message from '../img/msg.png'
import Bell from '../img/bell.png'
function Navbar() {
  const [search, setSearch] = useState("")
  const [userDetails, setUserDetails] = useState([])
  const { state, dispatch } = useContext(UserContext);
  const searchModel = useRef(null)
  

  
  useEffect(() => {
    M.Modal.init(searchModel.current)
  }, [])

  const history = useNavigate()
  const renderList = () => {
    if (state) {
      return [
        <li key="1"><i className="large material-icons modal-trigger" data-target="modal1" style={{ color: "black" }}>search</i>  </li>,
        <li key="1.2">
          <div className="icons">
          <div className="icon"> 
            <img src={Message} className='iconImage' alt='icon-n'/>
            <div className="counters">2</div>
          </div>
          <div className="icon"><img src={Bell} className='iconImage' alt='icon-n'/><div className="counters">2</div></div>
          <div className="icon"><img src={Notificatoin} className='iconImage' alt='icon-n'/><div className="counters">2</div></div>
          </div>
        </li>,
        <li key="2"><Link to="/profile">Profile</Link></li>,
        <li key="3"><Link to="/pro">Ui</Link></li>,
        <li key="3.1"><Link to="/chat">Chat</Link></li>,
        <li key="4"><Link to="/create">Create Post</Link></li>,
        <li key="5"><Link to="/myFollowingPost">My following Posts</Link></li>,
        <li key="6">
          <button className='btn #c62828 red darken-3'
            onClick={() => {
              localStorage.clear()
              dispatch({ type: "CLEAR" })
              history('signin')
            }}
          >
            Logout
          </button>
        </li>
      ]
    } else {
      return [
        <li key="7"><Link to="/signin">Sign in</Link></li>,
        <li key="8"><Link to='/signup'>Sign up</Link> </li>,
      ]
    }
  }

  const fetchUser = (query)=>{    
    setSearch(query)
    if(query.length >0){
    fetch('/searchUser',{
      method:'post',
      headers:{
        "Content-type":"application/json"
      },
      body:JSON.stringify({
        query:query
      })
    }).then(res=>res.json())
    .then(result=>{
      setUserDetails(result.user)     
    })
  }setUserDetails([])
}
  return (
    <nav>
      <div className="nav-wrapper  a"
        style={{ color: 'black' }}
      >
        <Link to="/"
          className="brand-logo left">
         Collagebook
        </Link>
        <ul id="nav-mobile" className="right hide-on-med-and-down">

          {renderList()}

        </ul>
      </div>
      <div id="modal1" className="modal" ref={searchModel} style={{color:"black"}}> 
        <div className="modal-content">
        <input type="text" placeholder='search user' value={search}
        onChange={(e)=>fetchUser(e.target.value)}/>
        <ul className="collection"  style={{color:"black" ,display: "grid"}}>
          {userDetails.map(item=>{
            return <Link
            to={ item._id !== state._id ? "/profile/"+item._id : "/profile"}
            onClick={()=>{
              M.Modal.getInstance(searchModel.current).close()
              setSearch("")
              setUserDetails([])
            }}><li className="collection-item" id={item.name}>{item.name}</li></Link>
          })}
           
        </ul>  

        </div>
        <div className="modal-footer">
          <button className="modal-close waves-effect waves-green btn-flat"
          onClick={()=>{setSearch("") 
          setUserDetails([])}}
          >close</button>
        </div>
      </div>
    </nav>
  )
}

export default Navbar