import { Box, Button, Drawer, IconButton, Skeleton, Toolbar, Typography } from '@mui/material'
import React from 'react'
import { Link } from 'react-router-dom'

function SideDrawer() {
  return (
    <div>
         <Box sx={{ flexGrow: 1 }}>
            Appbar
            <Toolbar>
                <IconButton
                 size="large"
                 edge="start"
                 aria-label="menu"
                 sx={{ mr: 2, color: "black" }}
                //  onClick={() => setIsDrawerOpen(true)}
               >
                Search..
               </IconButton>
               <Typography
              variant="h4"
              component="div"
              sx={{ flexGrow: 1, color: "black" }}
            >
                <Link to="/">Message</Link>
            </Typography>
            </Toolbar>
         </Box>
         <div>
        <Drawer
        >
            <Box p={2} width="270px" textAlign="center" role="presentation"
            >
            <Box
              sx={{
                display: "flex",
                alignItems: "center",
                "& > :not(style)": { m: 1 },
              }}
            >
                <input placeholder="search users"/>
                <Button variant="contained">Gosss</Button>
            </Box>
            <Skeleton />
            <Skeleton animation="wave" />
            <Skeleton animation={false} />
            </Box>
        </Drawer>
        </div>
    </div>
  )
}

export default SideDrawer